.straight-login{
    position: relative;
    margin-top: 20px;
    text-align: center;
    color: #666;
    font-size: 14px;
}
//快捷登录样式

.straight-login :before{
    width: 110px;
    height: 1px;
    position: absolute;
    content: "";
    left: 0;
    background: #ccc;
    top: 10px;
}

.straight-login :after{
    width: 110px;
    height: 1px;
    position: absolute;
    content: "";
    left: 190px;
    background: #ccc;
    top: 10px;
}
//快捷登录两边线条

.straight-login-content{
    display: inline-block;
    margin: 20px 0 20px 0;
    width: 100%;



}
//快捷图标
.straight-login-content li{
    font-size: 24px;
    float: left;
    display: inline-block;
    line-height: 40px;
    height:40px;
    width: 33.333%; 
    color: white;
    text-align: center;
    
}
